import React, { useState, useEffect } from 'react';
import { collection, query, getDocs, doc, updateDoc, deleteDoc } from 'firebase/firestore';
import { db } from '../../config';
import { Button, Drawer, Input, DatePicker, Pagination, Empty, Avatar, message, Modal, Space } from 'antd';
import moment from 'moment';
import Loading from '../../components/Loading';
import NotFound from '../../components/NotFound';
import Ungvien from './Hoso/Ungvien';
import { UserOutlined, MailOutlined, PhoneOutlined, IdcardOutlined, DeleteOutlined, SaveOutlined } from '@ant-design/icons';
import { FaUserTie } from 'react-icons/fa';
import { toast } from 'react-toastify';

const Hosodaluu = () => {
    const [cvData, setCvData] = useState([]);
    const [filteredData, setFilteredData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [selectedCV, setSelectedCV] = useState(null);
    const [drawerOpen, setDrawerOpen] = useState(false);
    const [cvLoading, setCVLoading] = useState(false);
    const [searchTerm, setSearchTerm] = useState('');
    const [filterDate, setFilterDate] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [isUnsaveModalVisible, setIsUnsaveModalVisible] = useState(false);
    const [isDeleteModalVisible, setIsDeleteModalVisible] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [cvToModify, setCvToModify] = useState(null);

    const pageSize = 20;

    useEffect(() => {
        const fetchCVData = async () => {
            try {
                const q = query(collection(db, 'CV'));
                const querySnapshot = await getDocs(q);
                const data = querySnapshot.docs
                    .map(doc => ({
                        id: doc.id,
                        ...doc.data()
                    }))
                    .filter(cv => cv.LuuHoSo === true);

                const sortedData = data.sort((a, b) =>
                    moment(b.NgayGuiCV, 'DD/MM/YYYY').valueOf() - moment(a.NgayGuiCV, 'DD/MM/YYYY').valueOf()
                );

                setCvData(sortedData);
                setFilteredData(sortedData);
                setLoading(false);
            } catch (err) {
                console.error("Error fetching CV data: ", err);
                setError("An error occurred while fetching data");
                setLoading(false);
            }
        };

        fetchCVData();
    }, []);

    useEffect(() => {
        const filtered = cvData.filter(cv => {
            const matchesSearch = cv.HoTen.toLowerCase().includes(searchTerm.toLowerCase()) ||
                cv.Email.toLowerCase().includes(searchTerm.toLowerCase()) ||
                cv.Vitriungtuyen.toLowerCase().includes(searchTerm.toLowerCase());

            const matchesDate = filterDate ? moment(cv.NgayGuiCV, 'DD/MM/YYYY').isSame(filterDate, 'day') : true;

            return matchesSearch && matchesDate;
        });

        setFilteredData(filtered);
        setCurrentPage(1);
    }, [searchTerm, filterDate, cvData]);

    const handleViewCV = (cv) => {
        setSelectedCV(cv);
        setCVLoading(true);
        setDrawerOpen(true);
        setTimeout(() => setCVLoading(false), 1500);
    };

    const handleSearch = (e) => {
        setSearchTerm(e.target.value);
    };

    const handleDateChange = (date) => {
        setFilterDate(date);
    };

    const handleUnsaveCV = (cv) => {
        setCvToModify(cv);
        setIsUnsaveModalVisible(true);
    };

    const handleDeleteCV = (cv) => {
        setCvToModify(cv);
        setIsDeleteModalVisible(true);
    };

    const confirmUnsaveCV = async () => {
        setIsLoading(true);
        try {
            const cvRef = doc(db, 'CV', cvToModify.id);
            await updateDoc(cvRef, {
                LuuHoSo: false
            });
            toast.success('Đã bỏ lưu hồ sơ.', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            setCvData(prevData => prevData.filter(item => item.id !== cvToModify.id));
            setFilteredData(prevData => prevData.filter(item => item.id !== cvToModify.id));
            setIsUnsaveModalVisible(false);
        } catch (error) {
            console.error("Error unsaving CV: ", error);
            message.error('Có lỗi xảy ra khi bỏ lưu hồ sơ');
        } finally {
            setIsLoading(false);
        }
    };

    const confirmDeleteCV = async () => {
        setIsLoading(true);
        try {
            const cvRef = doc(db, 'CV', cvToModify.id);
            await deleteDoc(cvRef);
            toast.success('Đã xóa hồ sơ.', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            setCvData(prevData => prevData.filter(item => item.id !== cvToModify.id));
            setFilteredData(prevData => prevData.filter(item => item.id !== cvToModify.id));
            setIsDeleteModalVisible(false);
        } catch (error) {
            console.error("Error deleting CV: ", error);
            message.error('Có lỗi xảy ra khi xóa hồ sơ');
        } finally {
            setIsLoading(false);
        }
    };

    const paginatedData = filteredData.slice((currentPage - 1) * pageSize, (currentPage) * pageSize);

    if (loading) return <Loading />;
    if (error) return <NotFound />;

    return (
        <>
            <div className="mb-4">
                <Input
                    placeholder="Tìm kiếm theo tên, email hoặc vị trí"
                    onChange={handleSearch}
                    size='large'
                    style={{ marginBottom: 15 }}
                />
                <Space>
                    <DatePicker
                        onChange={handleDateChange}
                        format="DD/MM/YYYY"
                        placeholder="Lọc theo ngày gửi CV"
                    />
                </Space>
            </div>

            {filteredData.length === 0 ? (
                <Empty description="Không tìm thấy hồ sơ nào đã lưu" />
            ) : (
                <>
                    <div className="row">
                        {paginatedData.map(cv => (
                            <div key={cv.id} className="col-md-3 mb-4">
                                <div className="card">
                                    <div className="card-body lh-lg">
                                        <div className="d-flex align-items-center">
                                            <Avatar
                                                size={48}
                                                src={cv.Hinhdaidien}
                                                icon={!cv.Hinhdaidien && <UserOutlined />}
                                                className="me-3"
                                            />
                                            <h2 className="card-title" style={{
                                                maxWidth: '250px',
                                                whiteSpace: 'nowrap',
                                                overflow: 'hidden',
                                                textOverflow: 'ellipsis'
                                            }}>
                                                {cv.Vitriungtuyen}
                                            </h2>
                                        </div>
                                        <div className="apply-form__group-body--line"></div>
                                        <div className='d-flex justify-content-between align-items-center mt-2'>
                                            <Ungvien cv={cv} />
                                        </div>
                                        <div className="apply-form__group-body--line"></div>
                                        <p className="card-text">
                                            <FaUserTie /> Tên ứng viên: {cv.HoTen}
                                        </p>
                                        <p className="card-text">
                                            <MailOutlined /> Email:
                                            <a href={`mailto:${cv.Email}`} className="ms-2">{cv.Email}</a>
                                        </p>
                                        <p className="card-text">
                                            <PhoneOutlined /> Số điện thoại:
                                            <a href={`tel:${cv.SoDienThoai}`} className="ms-2">{cv.SoDienThoai}</a>
                                        </p>
                                        <p className="card-text">
                                            <IdcardOutlined /> Ngày gửi CV: {cv.NgayGuiCV}
                                        </p>
                                        <div className="d-flex justify-content-between mt-3">
                                            {cv.CVFileUT && (
                                                <Button onClick={() => handleViewCV(cv)}>Xem CV File</Button>
                                            )}
                                            <Button
                                                icon={<SaveOutlined />}
                                                onClick={() => handleUnsaveCV(cv)}
                                            >
                                                Bỏ lưu
                                            </Button>
                                            <Button
                                                icon={<DeleteOutlined />}
                                                onClick={() => handleDeleteCV(cv)}
                                                danger
                                            >
                                                Xóa
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                    <Pagination
                        current={currentPage}
                        total={filteredData.length}
                        pageSize={pageSize}
                        onChange={setCurrentPage}
                        showSizeChanger={false}
                        className="mt-4"
                    />
                </>
            )}

            <Drawer
                title={<>CV của {selectedCV?.HoTen}</>}
                placement="right"
                onClose={() => setDrawerOpen(false)}
                open={drawerOpen}
                width={800}
            >
                {cvLoading ? (
                    <div className="d-flex justify-content-center">
                        <div className="spinner-border" role="status">
                            <span className="visually-hidden">Loading...</span>
                        </div>
                    </div>
                ) : (
                    selectedCV?.CVFileUT && (
                        <>
                            <iframe
                                src={selectedCV.CVFileUT}
                                style={{ width: '100%', height: '86vh', border: 'none' }}
                                title={`CV của ${selectedCV.HoTen}`}
                            />
                            <Button onClick={() => window.open(`${selectedCV.CVFileUT}`, '_blank')} type='primary'>Xem đầy đủ</Button>
                        </>
                    )
                )}
            </Drawer>

            <Modal
                title="Xác nhận"
                visible={isUnsaveModalVisible}
                onOk={confirmUnsaveCV}
                onCancel={() => setIsUnsaveModalVisible(false)}
                confirmLoading={isLoading}
                okText="Bỏ lưu"
                cancelText="Hủy"
            >
                Bạn có chắc chắn muốn bỏ lưu hồ sơ của <strong>{cvToModify?.HoTen}</strong>?
            </Modal>

            <Modal
                title="Xác nhận"
                visible={isDeleteModalVisible}
                onOk={confirmDeleteCV}
                onCancel={() => setIsDeleteModalVisible(false)}
                confirmLoading={isLoading}
                okText="Xóa"
                cancelText="Hủy"
            >
                Bạn có chắc chắn muốn xóa hồ sơ của <strong>{cvToModify?.HoTen}</strong>?
            </Modal>
        </>
    );
};

export default Hosodaluu;